.c {
    height: 100vh;
    position: relative;
}

.c-bg {
    width: 20px;
    height: 100%;
    background-color: #59b256;
    position: absolute;

}

.c-wrapper {
    padding: 50px;
    display: flex;
}

.c-left {

    flex: 1;
}

.c-right {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.c-title {
    font-size: 60px;
    width: 80%;
}

.c-info-item {
    display: flex;
    align-items: center;
    margin: 50px 0px;
    font-weight: 300;
    width: 70%;
}

.c-icon {
    width: 30px;
    height: 30px;
    margin-right: 20px;
}

.c-desc {
    font-weight: 200;
}

input {
    width: 60%;
    height: 50px;
    border: none;
    /* border-bottom: 1px solid black; */
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
    background: transparent;
}

textarea{
    width: 100%;
    background: transparent;
}

button{
    border: none;
    padding: 15px;
    background-color:seagreen;
    color: white;
    font-weight: 500;
    cursor: pointer;
}

.pb-5,.py-5 {
    padding-bottom: 3rem;
}
.pt-5, .py-5 {
    padding-top: 3rem;
}

.container {
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}

.copyright-text {
    font-size: 16px;
    font-weight: normal;
    display: block;
}
.text-center {
    text-align: center;
}
@media screen and (max-width : 480px) {
    .c-wrapper{
        flex-direction: column;
        padding: 0px 50px;
    }

    .c-title{
        font-size: 30px;
    }

    .c-info-item{
        margin: 7px 0px;
        width: 100%;
    }

    form{
        margin-top: 0px;
        display: flex;
        flex-wrap: wrap;
    }

    input{
        width: 35%;
        height: 50px;
        margin: 10px;
        margin-left: 0;
    }
    
    button{
        margin-top: 10px;
    }


}